<template>
    <div class="sidebar">
        <el-menu
            class="sidebar-el-menu"
            :default-active="onRoutes"
            :collapse="collapse"
            background-color="#324157"
            text-color="#bfcbd9"
            active-text-color="#20a0ff"
            unique-opened
            router
        >
            <template v-for="item in items">
                <template v-if="item.subs">
                    <el-submenu :index="item.index" :key="item.index">
                        <template slot="title">
                            <i :class="item.icon"></i>
                            <span slot="title">{{ item.title }}</span>
                        </template>
                        <template v-for="subItem in item.subs">
                            <el-submenu
                                v-if="subItem.subs"
                                :index="subItem.index"
                                :key="subItem.index"
                            >
                                <template slot="title">{{ subItem.title }}</template>
                                <el-menu-item
                                    v-for="(threeItem,i) in subItem.subs"
                                    :key="i"
                                    :index="threeItem.index"
                                >{{ threeItem.title }}</el-menu-item>
                            </el-submenu>
                            <el-menu-item
                                v-else
                                :index="subItem.index"
                                :key="subItem.index"
                            >{{ subItem.title }}</el-menu-item>
                        </template>
                    </el-submenu>
                </template>
                <template v-else>
                    <el-menu-item :index="item.index" :key="item.index">
                        <i :class="item.icon"></i>
                        <span slot="title">{{ item.title }}</span>
                    </el-menu-item>
                </template>
            </template>
        </el-menu>
    </div>
</template>

<script>
import bus from '../common/bus';
export default {
    data() {
        return {
            collapse: false,
            items:''
        };
    },
    mounted(){
        let type = localStorage.getItem('type');
        if (type==1) {
            this.items = [
                {
                    icon: 'el-icon-s-home',
                    index: 'dashboard',
                    title: '系统首页'
                },
                {
                    icon: 'el-icon-c-scale-to-original',
                    index: 'course',
                    title: '课程设计'
                },
                {
                    icon: 'el-icon-notebook-2',
                    index: 'works',
                    title: '作业项目'
                },
                {
                    icon: 'el-icon-folder-opened',
                    index: 'datas',
                    title: '基础资料'
                },
                {
                    icon: 'el-icon-message',
                    index: 'message',
                    title: '消息中心'
                },
            ]
        }else if(type==0){
            this.items = [
                {
                    icon: 'el-icon-s-home',
                    index: 'dashboard',
                    title: '系统首页'
                },
                {
                    icon: 'el-icon-c-scale-to-original',
                    index: 'stucourse',
                    title: '课程设计'
                },
                {
                    icon: 'el-icon-message',
                    index: 'stumessage',
                    title: '消息中心'
                }
            ]
        }else if(type==2){
            this.items = [
                {
                    icon: 'el-icon-s-home',
                    index: 'dashboard',
                    title: '系统首页'
                },
                {
                    icon: 'el-icon-s-check',
                    index: 'stumanage',
                    title: '学生管理'
                },
                {
                    icon: 'el-icon-s-custom',
                    index: 'teachermanage',
                    title: '教师管理'
                },
                {
                    icon: 'el-icon-s-opportunity',
                    index: 'admin',
                    title: '管理员管理'
                },
                {
                    icon: 'el-icon-s-operation',
                    index: 'schoolmanage',
                    title: '学习组织管理'
                }
            ]
        }
    },
    computed: {
        onRoutes() {
            return this.$route.path.replace('/', '');
        }
    },
    created() {
        // 通过 Event Bus 进行组件间通信，来折叠侧边栏
        bus.$on('collapse', msg => {
            this.collapse = msg;
            bus.$emit('collapse-content', msg);
        });
        let type = localStorage.getItem('type');
        let username = localStorage.getItem('ms_username');
        if(!username){
            if (type==1) {
                this.$router.push({path:'login1'})
            }else if(type==2){
                this.$router.push({path:'login2'})
            }else{
                this.$router.push({path:'login'})
            }
        }
    }
};
</script>

<style scoped>
.sidebar {
    display: block;
    position: absolute;
    left: 0;
    top: 70px;
    bottom: 0;
    overflow-y: scroll;
}
.sidebar::-webkit-scrollbar {
    width: 0;
}
.sidebar-el-menu:not(.el-menu--collapse) {
    width: 250px;
}
.sidebar > ul {
    height: 100%;
}
</style>



